.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  margin: auto;
}
.card {
  margin: 10px;
  padding: 20px;
  width: 45%;
  text-decoration: none;
}
input {
  margin: 10px;
  border: 1px solid rgba(34, 35, 58, 0.2);
  text-align: center;
  width: 50%;
}
button {
  margin: 10px;
}

input:focus {
  box-shadow: 0px 14px 80px rgb(4, 15, 224);
  padding: 20px;
}
.card:hover {
  box-shadow: 0px 14px 80px rgb(4, 15, 224);
  padding: 20px;
}
.btn:hover {
  box-shadow: 0px 14px 80px rgb(4, 15, 224);
  padding: 20px;
}
